import {tns as tinySlider} from "tiny-slider/src/tiny-slider";
import {md, lg} from "@main/consts/media";

/**
 * Класс для управления слайдерами
 */
export class Slider {
    /**
     * @param params
     * @param callbacks
     * @param methods
     */
    constructor({
        params = {
            container: ".tiny-slider",
            preventScrollOnTouch: "auto",
        },
        callbacks = {},
        methods = {}
    }) {
        if (params) {
            Object.keys(params).forEach(key => {
                if (typeof params[key] === "function") {
                    const fn = params[key];

                    params[key] = () => fn.call(this, this);
                }
            });
        }

        this.options = params;
        this.selector = this.options.container;
        this.callbacks = callbacks;
        this.methods = methods;
        this.tinySlider = undefined;

        this.mount();
    }

    /**
     * Инициализация слайдера с опциями
     */
    mount() {
        this.container = document.querySelector(this.selector);

        if (this.container) {
            this.tinySlider = tinySlider(this.options);

            this.bindSliderFunctions();
        }
    }

    /**
     * Возвращает информацию по текущему слайдеру.
     * Функция для сокращения существующей записи.
     *
     * @returns {TinySliderInfo}
     */
    getInfoAboutSlider() {
        return this.tinySlider.getInfo();
    }

    /**
     * Подключение функций для слайдера
     */
    bindSliderFunctions() {
        let callbacks = this.callbacks;
        let methods = this.methods;

        if (callbacks) {
            Object.keys(callbacks).forEach(key => {
                if (typeof callbacks[key] === "function") {
                    this.tinySlider.events.on(key, () => callbacks[key].call(this, this));
                }
            })
        }

        if (methods) {
            Object.keys(methods).forEach(key => {
                if (typeof methods[key] === "function") {
                    methods[key].call(this, this);
                }
            })
        }
    }  

    /**
     * Переключение активного слайда при клике на слайд с указанным селектором.
     *
     * @param selector
     * @param slideIndex
     */
    handleGoToSlideClick(selector = ".history-slide") {
        const thumbs = this.container.querySelectorAll(selector);

        if (window.innerWidth < lg) {
            thumbs.forEach((thumb) => {
                thumb.addEventListener("touchmove", () => {
                    const container = document.querySelector(".js-dots-nav-container");
                    const activeDot = container.querySelector(".tns-nav-active");
                    activeDot.scrollIntoView({block: "nearest", inline: "center", behavior: "smooth"});
                });
            })
        }
    }

    /**
     * Расчет размеров слайда с контентом в зависимости от положения активной точки с годом.
     * @param container
     * @param dots
     */
    translateHistorySlider() {

        const sectionContainer = document.querySelector(".js-history-section-container");
        const paddingSlider = parseInt(getComputedStyle(sectionContainer).paddingLeft)*2;

        if (window.innerWidth < md) {
            return;
        }

        const container = document.querySelector(".js-dots-nav-container");
        const activeDot = container.querySelector(".tns-nav-active");
        const positionActiveDot = activeDot.getBoundingClientRect().left;
        const sliderContainer = document.querySelector(".js-history-container");
        const activeSlideWrapper = activeDot.querySelector(".js-slide-wrapper");
        const activeSliderLine = activeDot.querySelector(".js-history-slide-line");
        const slideLines = document.querySelectorAll(".js-history-slide-line");
        const activeSlideContent = activeDot.querySelector(".js-slide-content");
        const slidesContent = document.querySelectorAll(".js-slide-content");
        const sliderContainerRect = sliderContainer.getBoundingClientRect();
        const sliderContainerWidth = parseInt(sliderContainerRect.width);
        const sliderContainerLeft = parseInt(sliderContainerRect.left);
        const sliderContainerWithoutPaddings = sliderContainerWidth - paddingSlider;

        activeSlideWrapper.style.width = Math.abs(window.innerWidth - positionActiveDot - paddingSlider - sliderContainerLeft) + "px";
        const activeSlideWrapperHeight = activeSlideWrapper.getBoundingClientRect().height;

        sliderContainer.style.paddingBottom = 0;
        sliderContainer.style.paddingBottom = activeSlideWrapperHeight + "px";

        slideLines.forEach(line => this.removeClass(line, "transitioned"));
        slidesContent.forEach(slide => this.removeClass(slide, "visible"));

        activeSliderLine.style.left = activeDot.clientWidth / 2 + "px";
        activeSliderLine.classList.add("transitioned");
        activeSlideContent.classList.add("visible");

        if (Math.abs(parseInt(activeSlideWrapper.style.width)) <= (sliderContainerWithoutPaddings/2)) {
            activeSlideWrapper.style.left = "auto";
            activeSlideWrapper.style.right = "100%";
            activeSlideWrapper.classList.add("is-reverse");
            activeSlideWrapper.style.width = positionActiveDot - sliderContainerLeft + "px";
            const activeSlideWrapperHeight = activeSlideWrapper.getBoundingClientRect().height;
            sliderContainer.style.paddingBottom = activeSlideWrapperHeight + "px";
            activeSliderLine.style.left = "auto";
            activeSliderLine.style.right = - activeDot.clientWidth / 2 + "px";
        } else {
            activeSlideWrapper.style.left = "0";
            activeSlideWrapper.style.right = "auto";
            activeSlideWrapper.classList.remove("is-reverse");
        }

        if(positionActiveDot < 0 ) {
            activeSlideWrapper.style.paddingLeft = Math.abs(positionActiveDot) + "px";
            activeSlideWrapper.style.width = sliderContainer.clientWidth + Math.abs(positionActiveDot) + "px";
        } else if (positionActiveDot > sliderContainer.getBoundingClientRect().right) {
            activeSlideWrapper.style.paddingRight = positionActiveDot - sliderContainer.clientWidth + activeDot.clientWidth + "px";
            activeSlideWrapper.style.width = sliderContainer.clientWidth + "px";
        } else {
            activeSlideWrapper.style.paddingLeft = "0";
            activeSlideWrapper.style.paddingRight = "0";
        }
    }

    removeClass(item, className) {
        item.classList.remove(className);
    }
}
