/**
 * Компонент открывает/закрывает меню.
 */
import {lg} from "@main/consts/media";
import {getBtnFromTarget} from "@main/utils/get-btn-from-target";

class ToggleMenu {
    /**
     * Создает компонент, который открывает/закрывает меню.
     *
     */
    constructor() {
        this.bindEventListeners();
    }

    /**
     * Событие клика на документ
     *
     * @returns {void}
     */
    bindEventListeners() {
        document.addEventListener("click", (e) => {
            const btn = getBtnFromTarget(e.target, "js-toggle-menu");
            if (btn) {
                e.preventDefault();
                if (document.body.classList.contains("menu-opened")) {
                    this.closeMenu();
                } else {
                    this.openMenu(btn);
                }
            } else if (!getBtnFromTarget(e.target, "js-menu")) {
                this.closeMenu();
            }
        });
    }

    closeMenu() {
        if (window.innerWidth < lg) {
            document.body.classList.remove("prevent-scroll");
        }
        document.body.classList.remove("menu-opened");
        document.querySelectorAll(".js-menu").forEach((menu) => menu.classList.remove("opened"));
    }

    openMenu(btn) {
        const type = btn.dataset.type;
        const menus = document.querySelectorAll(".js-menu[data-type='" + type + "']")
        if (window.innerWidth < lg) {
            document.body.classList.add("prevent-scroll");
        }
        document.body.classList.add("menu-opened");
        menus.forEach((menu) =>  menu.classList.add("opened"));
    }
}

new ToggleMenu();
