/** Выпадающий список с выбором опций. */
import DropdownField from "@main/components/dropdown/dropdown-field";

export default class DropdownSelect extends DropdownField {
    /**
     * Конструктор класса.
     *
     * @param {HTMLElement} el Элемент.
     */
    constructor(el) {
        super(el);
        this.buttons = Array.from(this.el.querySelectorAll(".js-dropdown-select-button"));
        this.radio = Array.from(this.el.querySelectorAll(".js-radio"));

        this.bindEventListeners();
    }

    /**
     * Инициализация эвентов.
     *
     * @returns {void}
     */
    bindEventListeners() {
        super.bindEventListeners();

        this.buttons.forEach((button) => {
            button.addEventListener("click", () => {
                this.selectOption(button);
            });
        });
    }

    /**
     * Выбор опции селектбокса.
     *
     * @param {HTMLElement} button Текущая опция селекбокса.
     * @returns {void}
     */
    selectOption(button) {
        const text = button.dataset.text || button.innerText;
        this.el.classList.remove("open");
        this.el.classList.add("selected");
        this.text.innerText = text;
        document.querySelector("#" + button.getAttribute("for")).checked = true;
        this.form.dispatchEvent(new Event("submitAjax"));
        this.checkFilterIsCLear();
    }

    /**
     * Проверить заполненность поля
     *
     * @returns {boolean}
     */
    isSelected() {
        return this.radio.filter((radio) => radio.checked).length > 0;
    }

    /**
     * Очистить значение
     *
     * @param {boolean} submitForm Флаг отправить данные формы или нет.
     * @returns {void}
     */
    clear(submitForm) {
        super.clear(submitForm);
        this.radio.forEach((radio) => {
            radio.checked = false;
            radio.removeAttribute("checked");
        });
        if (submitForm) {
            this.form.dispatchEvent(new Event("submitAjax"));
        }
    }
}

document.querySelectorAll(".js-dropdown-select").forEach((elem) => new DropdownSelect(elem));
