import {Slider} from "@main/components/sliders/history/tiny-slider";
import {md, lg, xl} from "@main/consts/media";

document.addEventListener("DOMContentLoaded", () => {
    const sliders = [{
        params: {
            container: ".js-history-slider",
            mode: "gallery",
            speed: 100,
            items: 1,
            gutter: 40,
            controls: false,
            nav: true,
            navPosition: "top",
            mouseDrag: true,
            touch: true,
            navContainer: ".js-dots-nav-container",
            loop: false,
            controlsText: ["", ""],
            navAsThumbnails: true,
            preventScrollOnTouch: "force",
            preventActionWhenRunning: true, 
            swipeAngle: 30,
            freezable: false
        },
        callbacks: {
            transitionEnd: slider => {
                slider.handleGoToSlideClick();
                slider.translateHistorySlider({
                    container: ".js-history-dots",
                    dots: ".js-history-dot"
                });
            },
        }
    },{
        params: {
            loop: false,
            container: ".js-dots-nav-container",
            items: 3,
            controls: false,
            mouseDrag: true,
            nav: false,
            navAsThumbnails: false,
            arrowKeys: false,
            controlsText: ["", ""],
            touch: true,
            freezable: true,
            preventScrollOnTouch: "force",
            preventActionWhenRunning: true,
            swipeAngle: 30,
            responsive: {
                [md]: {
                    items: 8
                },
                [lg]: {
                    items: 10
                },
                [xl]: {
                    items: 12
                }
            },
            onInit: slider => {
                slider.translateHistorySlider({
                    container: ".js-history-dots",
                    dots: ".js-history-dot"
                });
            }
        },
        callbacks: {
            transitionEnd: slider => {
                slider.handleGoToSlideClick();
                slider.translateHistorySlider({
                    container: ".js-history-dots",
                    dots: ".js-history-dot"
                });
            }
        },
    },  ];

    sliders.forEach(el => {
        new Slider(el);
    });
});
