/** Поиск с подсказками. */
import {Autocomplete} from "@main/components/autocomplete";

export default class SearchHints {
    /**
     * Конструктор класса.
     *
     * @param {HTMLElement} el Элемент.
     */
    constructor(el) {
        this.el = el;
        this.form = this.el.closest("form");
        this.input = this.el.querySelector(".js-autocomplete-input");
        this.action = this.form.dataset.action;
        this.initAutocomplete();
    }

    /**
     * Инициализация поля с подсказками.
     *
     * @returns {void}
     */
    initAutocomplete() {
        new Autocomplete(this.el, this.onValueSelect.bind(this));
    }

    /**
     * При выборе значения из списка найденного
     *
     * @returns {void}
     */
    onValueSelect() {}
}

document.querySelectorAll(".js-filter-hints").forEach((elem) => new SearchHints(elem));
