import Swiper, {Navigation, Pagination} from "swiper";
import {sm0, sm1, lg} from "@main/consts/media";
Swiper.use([Navigation]);
Swiper.use([Pagination]);
/** Класс слайдера с вакансиями. */
class VacancySlider {
    /**
     * Конструктор класса.
     *
     * @param {HTMLElement} el Элемент.
     */
    constructor(el) {
        this.el = el;
        this.swiperElement = this.el.querySelector(".js-vacancy-slider-swiper");
        this.btnPrev = this.el.querySelector(".js-vacancy-slider-prev");
        this.btnNext = this.el.querySelector(".js-vacancy-slider-next");
        this.initSlider();
    }

    /**
     * Инициализация слайдера.
     *
     * @returns {void}
     */
    initSlider() {
        new Swiper(this.swiperElement, {
            slidesPerView: 1,
            spaceBetween: 16,
            loop: true,
            navigation: {
                prevEl: this.btnPrev,
                nextEl: this.btnNext,
            },
            breakpoints: {
                [sm0]: {
                    slidesPerView: 1.1
                },
                [sm1]: {
                    slidesPerView: 2.1
                },
                [lg]: {
                    slidesPerView: 2
                }
            },
        });
    }
}


window.addEventListener("load", () => {
    document.querySelectorAll(".js-vacancy-slider").forEach((slider) => new VacancySlider(slider));
}, false);
