import {Accordion} from "@main/components/accordion/accordion";
import {md} from "@main/consts/media";

/**
 * Компонент аккордеона на планшете и мобилке.
 */
class TabletAccordion extends Accordion {
    /**
     * Создает компонент аккордеона.
     *
     * @param {HTMLElement} el Контейнер аккордеона.
     */
    constructor(el) {
        super(el);
        new ResizeObserver(this.onResizeWindow.bind(this)).observe(document.body);
    }
    /**
     * Перерисовка тела аккордеона.
     *
     * @returns {void}
     */
    updateRender() {
        if (window.innerWidth < md) {
            super.updateRender();
        }
    }

    /**
     * Обработка нажатия на хэдер.
     *
     * @returns {void}
     */
    onClickHeader() {
        if (window.innerWidth < md) {
            super.onClickHeader();
        }
    }

    /**
     * Обработка изменение размера вьюпорта.
     *
     * @returns {void}
     */
    onResizeWindow() {
        if (window.innerWidth >= md) {
            this.body.style.overflow = null;
            this.body.style.height = null;
        } else if (!this.active) {
            this.hideBody();
        }
    }
}

document.querySelectorAll(".js-accordion-tablet").forEach((el) => new TabletAccordion(el));
