import {Input} from "@main/components/form-field/input";
import {getFormattedFileSize} from "@main/utils/file-size-format";

/** Компонент выбора файла. */
export class FileInput extends Input {
    /**
     * Создает компонент выбора файла.
     *
     * @param {object} props Параметры компонента.
     * @param {HTMLElement} props.container Родительский элемент.
     * @param {HTMLInputElement} props.input Элемент ввода.
     */
    constructor(props) {
        super(props);
        this.findElements();
    }

    /**
     * Ищет элементы.
     *
     * @returns {void}
     */
    findElements() {
        this.fileNameCaptionNode = this.el.querySelector(".js-form-field-file-caption");
    }

    /**
     * Инициализация евентов.
     *
     * @returns {void}
     */
    bindEventListeners() {
        super.bindEventListeners();
        this.input.addEventListener("change", this.onChange.bind(this));
    }

    /**
     * Коллбек, который обрабатывает выбор файла.
     *
     * @returns {void}
     */
    onChange() {
        const [file] = this.input.files;
        this.fileNameCaptionNode.innerText = file ? `${file.name}, ${getFormattedFileSize(file.size)}` : "";
    }

    /**
     * Получить значение файла.
     *
     * @returns {File} Файл.
     */
    getValue() {
        return this.input.files[0];
    }
}
