import AirDatepicker from "air-datepicker";
import {Input} from "@main/components/form-field/input";
import {lg} from "@main/consts/media";
import {CONTAINER_PADDING} from "@main/consts";

/** 
 * Компонент выбора даты.
 */
export class Datepicker extends Input {
    /**
     * @type {AirDatepicker<HTMLInputElement>} инстанс AirDatepicker
     * @private
     */
    datepicker;

    /**
     * Создает компонент выбора даты.
     *
     * @param {object} props - Параметры компонента.
     * @param {HTMLElement} props.container - Родительский элемент.
     * @param {HTMLInputElement} props.input - Элемент ввода.
     * @param {object | undefined} props.options - Конфиг для инициализации AirDatepicker.
     */
    constructor({container, input, options}) {
        super({container, input});
        this.datepicker = Datepicker.initAirDatepicker(input, options);
    }

    /**
     * Инициализирует календарь.
     *
     * @param {HTMLInputElement} input - Элемент ввода.
     * @param {object | undefined} options - Конфиг для инициализации AirDatepicker.
     */

    static initAirDatepicker(input, options) {
        const container = input.closest(".js-calendar-container");
        if (window.innerWidth < lg) {
            if (!options) {
                options = {};
            }
            options.position = function({$datepicker, $target}) {
                const dpWidth = $datepicker.clientWidth;
                const rectTarget = $target.getBoundingClientRect();
                const rectContainer = container.getBoundingClientRect();
                const rightMaxPosition = rectTarget.x + dpWidth;
                if (rightMaxPosition > window.innerWidth) {
                    $datepicker.classList.add("-bottom-right-");
                    $datepicker.classList.remove("-bottom-left-");
                    $datepicker.style.left = (-1 * rectTarget.x + CONTAINER_PADDING) + "px";
                } else {
                    $datepicker.style.left = "0";
                    $datepicker.classList.add("-bottom-left-");
                    $datepicker.classList.remove("-bottom-right-");
                }
                const top = rectTarget.y - rectContainer.y + rectTarget.height;
                $datepicker.classList.add("-from-bottom-");
                $datepicker.style.top = `${top}px`;
            }
        }
        const picker = new AirDatepicker(input, {
            dateFormat: "dd.MM.yyyy",
            autoClose: true,
            prevHtml: `<svg 
                            class="air-datepicker-nav__action air-datepicker-nav__action--prev" />
            `,
            nextHtml: `<svg 
                            class="air-datepicker-nav__action air-datepicker-nav__action--next" />
            `,
            minDate: input.getAttribute("data-min-date") ?? undefined,
            maxDate: new Date(),
            container: container,
            onSelect: function() {
                let event = new Event("blur");
                input.dispatchEvent(event);

                const pickerParent = input.closest(".js-form-field");
                if(!pickerParent.classList.contains("filled")) {
                    pickerParent.classList.add("filled");
                }
            },
            ...options,
        });
        return picker;
    }

}
