import {NOTIFICATION_TIMEOUT_HIDE_INFO_MESSAGE} from "@main/consts/delay";
import {getCookie, setCookie} from "@main/utils/cookie";

/**
 * Всплывающее уведомление.
 */
export class Notification {
    /**
     * Создает компонент.
     *
     * @param {HTMLElement} el Элемент-уведомление.
     */
    constructor(el) {
        this.el = el;
        this.type = this.el.dataset.type;
        this.cookieName = this.el.dataset.cookie;
        this.btnCookie = this.el.querySelector(".js-button-cookies");
        this.timer = null;
        this.bindEventListeners();
        this.autoShow();
        this.showByCookie();
    }

    /**
     * Показать плашку с уведомлением. Закрыть по таймауту, если нужно.
     *
     * @returns {void}
     */
    show() {
        this.el.classList.add("visible");

        if (this.el.classList.contains("js-auto-close")) {
            clearTimeout(this.timer);
            this.timer = setTimeout(() => {
                this.close();
            }, NOTIFICATION_TIMEOUT_HIDE_INFO_MESSAGE);
        }
    }

    /**
     * Показать плашку с уведомлением автоматически при загрузке страницы
     *
     * @returns {void}
     */
    autoShow() {
        if(this.el.classList.contains("js-auto-notification")) {
            this.show();
        }
    }

    /**
     * Показать плашку с уведомлением в зависимости от куки.
     *
     * @returns {void}
     */
    showByCookie() {
        if (this.el.classList.contains("js-cookies-notification")) {
            if (getCookie(this.cookieName) !== "accept") {
                this.show();
            }
        }
    }

    /**
     * Закрыть плашку с уведомлением.
     *
     * @returns {void}
     */
    close() {
        this.el.classList.remove("visible");
    }

    /**
     * Обрабатываем клик. Закрываем плашку только если кликнули на ОК
     *
     * @returns {void}
     */
    bindEventListeners() {
        if (this.btnCookie) {
            this.btnCookie.addEventListener("click", () => {
                setCookie(this.cookieName, "accept");
                this.close();
            });
        }
    }
}

document.querySelectorAll(".js-notification").forEach((el) => new Notification(el));
