/**
 * Маска ввода даты.
 *
 * @param {HTMLInputElement} input - Элемент ввода.
 */
import {WITHOUT_DIGITS_REGEX} from "@main/consts/regex";

export const dateInputMask = (input) => {
    input.addEventListener("input", function() {
        const prefix = input.dataset.prefix || "";
        input.value = input.value.replace(prefix, "").replace(WITHOUT_DIGITS_REGEX, "");

        const len = input.value.length;
        if (len >= 2) {
            let day = input.value.slice(0, 2);
            let month = input.value.slice(2, 4);
            let year = input.value.slice(4, 8);

            if (Number(day) > 31) {
                day = 31;
            }
            if (Number(month) > 12) {
                month = 12;
            }

            input.value = day;
            if (month) {
                input.value = day + "." + month;
            }

            if (year) {
                input.value = day + "." + month+ "." + year;
            }
        }
        input.value = prefix + input.value;
    });
}
