import {Input} from "@main/components/form-field/input";

/** Класс поля пароля. */
export class PasswordInput extends Input {
    /**
     * Конструктор класса.
     *
     * @param {object} props Параметры компонента.
     * @param {HTMLElement} props.container Родительский элемент.
     * @param {HTMLInputElement} props.input Элемент ввода.
     */
    constructor(props) {
        super(props);
    }

    /**
     * Прослушиваем события в инпуте с телефоном.
     *
     * @returns {void}
     */
    bindEventListeners() {
        super.bindEventListeners();
        this.el.addEventListener("input", this.onInput.bind(this));
    }

    /**
     * Действия при вводе пароля.
     *
     * @param {Event} e Событие, происходящее при вводе в инпуте.
     * @returns {void}
     */
    onInput(e) {
        super.onInput();

        const input = e.target;
        this.toggleDisabledSubmitButton(input);
    }

    /**
     * Менять disabled у кнопки отправки формы, в зависимости от того, пустые поля или нет.
     *
     * @param {HTMLInputElement} input Поле пароля.
     * @returns {void}
     */
    toggleDisabledSubmitButton(input) {
        const passwordGroup = input.closest(".js-password-group");

        if (!passwordGroup) {
            return;
        }

        const inputs = passwordGroup.querySelectorAll("input");
        const btnSubmit = passwordGroup.closest(".js-form-wrapper").querySelector("button[type='submit']");
        if (inputs[0].value && inputs[1].value) {
            btnSubmit.removeAttribute("disabled");
        } else {
            btnSubmit.setAttribute("disabled", "disabled");
        }
    }
}
