import {Datepicker} from "@main/components/form-field/datepicker";
import {FileInput} from "@main/components/form-field/file-input";
import {Input} from "@main/components/form-field/input";
import {PhoneInput} from "@main/components/form-field/phone-input";
import {Select} from "@main/components/form-field/select";
import {PasswordInput} from "@main/components/form-field/password-input";
import {NumberInput} from "@main/components/form-field/number-input";
import {dateInputMask} from "@main/utils/date-input-mask";

/**
 * Инициализируются компоненты ввода.
 *
 * @param {Element} element HTML элемент для поиска.
 * @returns {void}
 */
export const initFormFields = (element) => {
    element.querySelectorAll(".js-form-field").forEach((el) => {
        const input = el.querySelector(".js-form-field-input");

        switch (input.type) {
            case "file":
                return new FileInput({container: el, input});

            case "select-multiple":
            case "select-one":
                return new Select({container: el, input});

            case "date":
                // to prevent appearance of default popup-calendar  in Firefox
                input.addEventListener("click", (e)=> {
                    e.preventDefault();
                })
                return new Datepicker({container: el, input});
            case "tel":
                return new PhoneInput({container: el, input});

            case "password":
                return new PasswordInput({container: el, input});

            default:
                if (input.classList.contains("js-form-field-input-number")) {
                    return new NumberInput({container: el, input});
                } else if (input.classList.contains("js-form-field-input-date")) {
                    dateInputMask(input);
                    return new Datepicker({container: el, input});
                } else {
                    return new Input({container: el, input});
                }
        }
    });
}
initFormFields(document);
