import {NOTIFICATION_TIMEOUT_HIDE_INFO_MESSAGE} from "@main/consts/delay";
import {getBtnFromTarget} from "@main/utils/get-btn-from-target";

/** Компонент модала. */
export class Modal {
    /**
     * Создаёт компонент модала.
     *
     * @param {HTMLElement} el Родительский элемент модала (элемент заднего фона).
     */
    constructor(el) {
        this.el = el;
        this.name = this.el.dataset.modalName;
        this.bindEventListeners();
        if (this.el.classList.contains("js-modal-opened")) {
            Modal.open(this.el);
        }
    }

    /**
     * Вешает колбеки закрытия модала.
     *
     * @returns {void}
     */
    bindEventListeners() {
        this.el.addEventListener("click", (e) => {
            if (!e.target.closest(".js-modal-dialog")
                && !getBtnFromTarget(e.target, "js-expanded-text")
                && !e.target.classList.contains("js-expanded-link")
                && !e.target.classList.contains("air-datepicker-cell")
                && !e.target.closest(".air-datepicker")
            ) {
                Modal.close(this.el);
            }
        });
        document.addEventListener("click", (e) => {
            if (e.target.classList.contains("js-modal-close")) {
                const curModal = e.target.closest(".js-modal");
                if (curModal) {
                    Modal.close(curModal);
                } else {
                    Modal.close(this.el);
                }
            } else {
                const btn = getBtnFromTarget(e.target, "js-modal-open");
                if (btn && btn.dataset.modal === this.name) {
                    e.preventDefault();
                    Modal.open(this.el);
                }
            }
        });
    }

    /**
     * Закрывает модал.
     *
     * @param {HTMLElement} modal Главный элемент модала.
     * @returns {void}
     */
    static close(modal) {
        modal.classList.remove("visible");
        document.body.classList.remove("prevent-scroll");
    }

    /**
     * Открывает модал.
     *
     * @param {Element} modal Главный элемент модала.
     * @returns {void}
     */
    static open(modal) {
        modal.classList.add("visible");
        document.body.classList.add("prevent-scroll");

        const input = modal.querySelector("input");
        if (input) {
            input.focus();
        }

        if (modal.classList.contains("js-modal-autoclose")) {
            Modal.autoclose(modal);
        }
    }

    /**
     * Закрыть модал по таймауту.
     *
     * @param {Element} modal Главный элемент модала.
     * @returns {void}
     */
    static autoclose(modal) {
        setTimeout(() => {
            Modal.close(modal);
        }, NOTIFICATION_TIMEOUT_HIDE_INFO_MESSAGE);
    }
}

document.querySelectorAll(".js-modal").forEach((el) => new Modal(el));
