/** Базовый класс выпадающего поля. */
import {CONTAINER_PADDING} from "@main/consts";

export default class DropdownField {
    /**
     * Конструктор класса.
     *
     * @param {HTMLElement} el Элемент.
     */
    constructor(el) {
        this.el = el;
        this.btn = this.el.querySelector(".js-dropdown-field-btn");
        this.text = this.el.querySelector(".js-dropdown-field-text");
        this.textDefault = this.text.dataset.text;
        this.clearBtn = this.el.querySelector(".js-dropdown-field-clear");
        this.filter = this.el.closest(".js-filter-params");
        this.dropdown = this.el.querySelector(".js-dropdown-block");
        this.form = this.el.closest(".js-feedback-form");
    }

    /**
     * Инициализация эвентов.
     *
     * @returns {void}
     */
    bindEventListeners() {
        this.btn.addEventListener("click", (e) => {
            if (e.target.classList.contains(".js-dropdown-field-clear") || e.target.closest(".js-dropdown-field-clear")) {
                this.clear(true);
            } else {
                this.toggleDropdown();
            }
        });

        this.clearBtn.addEventListener("click", () => {
            this.clear(true);
        });

        document.addEventListener("mouseup", (e) => {
            if (!e.target.classList.contains(".js-dropdown-field")
                && !e.target.closest(".js-dropdown-field")
                && !e.target.closest(".air-datepicker")
            ) {
                this.close();
            }
        });

        this.el.addEventListener("closeDropdownField", () => {
            this.close();
        });

        this.el.addEventListener("clearDropdownField", () => {
            this.clear(false);
        });
    }

    /**
     * Переключение выпадающего списка.
     *
     * @returns {void}
     */
    toggleDropdown() {
        const isOpen = this.el.classList.contains("open");

        if (isOpen) {
            this.close();
        } else {
            document.querySelectorAll(".js-dropdown-field").forEach((item) => item.dispatchEvent(new Event("closeDropdownField")));
            this.checkPosition();
            this.el.classList.add("open");
        }
    }

    /**
     * Закрыть выпадающее поле, проверить заполненность и добавить класс заполненности
     *
     * @returns {void}
     */
    close() {
        this.el.classList.remove("open");
        if (this.isSelected()) {
            this.el.classList.add("selected");
        }
        this.checkFilterIsCLear();
    }

    /**
     * Проверить заполненность поля
     *
     * @returns {boolean}
     */
    isSelected() {
    }

    /**
     * Очистить значение
     *
     * @param {boolean} submitForm Флаг отправить данные формы или нет.
     * @returns {void}
     */
    clear(submitForm) {
        this.text.innerHTML = this.textDefault;
        this.el.classList.remove("selected");
        this.el.classList.remove("open");
        if (submitForm) {
            this.checkFilterIsCLear();
        }
    }

    checkFilterIsCLear() {
        if (this.filter) {
            this.filter.dispatchEvent(new Event("checkFilterIsCLear"));
        }
    }

    /**
     * Проверить позицию выпадающего элемента и переместить его направо, если не влазит в экран.
     *
     * @returns {void}
     */
    checkPosition() {
        const rectMenu = this.dropdown.getBoundingClientRect();
        const rectEl = this.el.getBoundingClientRect();
        const leftMinPosition = rectEl.x - rectMenu.width;
        const rightMaxPosition = rectEl.x + rectMenu.width;
        if (leftMinPosition < 0 && rightMaxPosition > window.innerWidth) {
            this.dropdown.classList.remove("right");
            this.dropdown.style.left = (-1 * rectEl.x + CONTAINER_PADDING) + "px";
        } else if (rightMaxPosition > window.innerWidth) {
            this.dropdown.classList.add("right");
            this.dropdown.removeAttribute("style");
        } else {
            this.dropdown.classList.remove("right");
            this.dropdown.removeAttribute("style");
        }
    }
}
