/**
 * Textarea с автоувеличением высоты.
 */
export class AutoheightTextarea {
    /**
     * Создает компонент.
     *
     * @param {HTMLElement} el Элемент textarea.
     */
    constructor(el) {
        this.el = el;
        this.bindEventListeners();
    }

    /**
     * Привязка обработчика события ввода.
     *
     * @returns {void}
     */
    bindEventListeners() {
        this.el.addEventListener("input", () => {
            this.el.style.height = 0;
            this.el.style.overflowY = "hidden";
            this.el.style.height = (this.el.scrollHeight) + "px";
        });
    }
}

document.querySelectorAll(".js-autoheight-textarea").forEach((el) => new AutoheightTextarea(el));
