import Swiper from "swiper";
import {sm, md, lg, xl, xxl} from "@main/consts/media";
/** Класс слайдера с картинками на странице "Корпоративная жизнь". */
class CorporateLifeSlider {
    /**
     * Конструктор класса.
     *
     * @param {HTMLElement} el Элемент.
     */
    constructor(el) {
        this.el = el;
        this.initSlider();
    }

    /**
     * Инициализация слайдера.
     *
     * @returns {void}
     */
    initSlider() {
        new Swiper(this.el, {
            centeredSlides: true,
            loop: true,
            coverflowEffect: {
                slideShadows: true,
            },
            spaceBetween: 8,
            breakpoints: {
                [sm]: {
                    slidesPerView: 1.1,
                },
                [md]: {
                    slidesPerView: 1.08,
                    spaceBetween: 8,
                },
                [lg]: {
                    slidesPerView: 1.1,
                    spaceBetween: 24,
                },
                [xl]: {
                    slidesPerView: 1.13,
                    spaceBetween: 24,
                },
                [xxl]: {
                    slidesPerView: 1.4,
                    spaceBetween: 24,
                }
            },
        });
    }
}


window.addEventListener("load", () => {
    document.querySelectorAll(".js-corporate-life-slider").forEach((slider) => new CorporateLifeSlider(slider));
}, false);
